.weather-current {
  display: flex;
  justify-content: space-between;
  font-size: 2.4rem;
  align-items: center;
  width: 100%;
}

.temp {
  font-size: 3rem;
}

.place {
  padding: 0 1rem;
}

.condition {
  font-size: 3.2rem;
  display: flex;
}

.get {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.data-row {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  width: 100%;
  padding: 1rem 0;
  align-items: center;
  grid-column-gap: 3rem;
}

.data-row-current {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  padding: 1rem 0;
}

.bar {
  background: var(--background);
  width: 7rem;
  height: 0.5rem;
  border-radius: 3rem;
  position: relative;
}

.bar-percent {
  background: var(--green);
  height: 0.5rem;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 3rem;
}

.center {
  text-align: center;
}